.featured-users {
    border-bottom: 2px solid #F6F6F7;
    padding-top: 20px;
    padding-bottom: 40px;
	background: #f4f4f4;
	.section-title{
		margin-bottom: 0px !important;
		h1{
			margin-top: 10px;
			text-align: center;
			font-size: 35px;
			color: #000;
			font-family: 'Roboto';
			margin-bottom: 0;
		}
		margin-bottom: 50px;
	}
	.input-group { border-radius: 4px; position: relative;
		width: 98%;
		margin: auto;
		height: 55px;
		.form-control {border-color: transparent; border-radius: 4px; padding: 8px 135px 8px 25px; font-size: 14px; font-weight: 600; height: 100%;}
		.btn {font-size: 12px; 
			background-color: rgba($color: #345581, $alpha: 0.9);
			&:hover{
				background-color: rgba($color: #345581, $alpha: 1);
			}
			outline: 0;
			border: 0;
			font-family: 'HKGrotesk' ;
			letter-spacing: 1px;
			text-transform: uppercase; height: 40px; margin-top: 7px; margin-right: 15px; font-weight: 600; padding: 6px 25px; border-radius: 4px !important; position: absolute; right: 0px; z-index: 9;top: 0;}
	}
	.widget {
		.text-info{
			color: red !important;
			outline: none !important;
		}
		color: #afb1b2;
		// padding-top: 10px;
		border-radius: 4px;
		// background-color: #fff;
		margin-bottom: 30px;
		.categoryDiv{
			.MuiPaper-root{
				box-shadow: none;
				.MuiButtonBase-root{
					// padding: 0;
					min-height: 33px !important;
					background-color: #345581;
					border-radius: 5px 5px 0 0;
					.MuiExpansionPanelSummary-content{
						margin: 0;
						color: white;
					}
					.MuiButtonBase-root{
						padding: 0;
						color: white;
					}
					p{
						margin: 0;
						font-size: 16px;
						letter-spacing: 1px;
					}
				}
			}
			.MuiTypography-root{
				font-size: 14px;
			}
			.MuiCollapse-container{
				.MuiExpansionPanelDetails-root{
					padding-left: 3px;
					padding-right: 3px;
					padding-top: 30px;
					padding-bottom: 30px;
				}
			}
		}

		.countryDiv{
			.MuiPaper-root{
				box-shadow: none;
				.MuiButtonBase-root{
					// padding: 0;
					min-height: 33px !important;
					background-color: #345581;
					border-radius: 0px 0px 5px 5px;
					.MuiExpansionPanelSummary-content{
						margin: 0;
						color: white;
					}
					.MuiButtonBase-root{
						padding: 0;
						color: white;
					}
					p{
						margin: 0;
						font-size: 16px;
						letter-spacing: 1px;
					}
				}
			}
			.MuiTypography-root{
				font-size: 14px;
			}
			.MuiCollapse-container{
				.MuiExpansionPanelDetails-root{
					padding-left: 3px;
					padding-right: 3px;
					padding-top: 30px;
					padding-bottom: 30px;
				}
				.multi__control{
					min-height: 38px;
				}
			}
		}

		.paymentDiv{
			.MuiPaper-root{
				box-shadow: none;
				.top{
					// padding: 0;
					min-height: 33px !important;
					background-color: #345581;
					border-radius: 0px;
					.MuiExpansionPanelSummary-content{
						margin: 0;
						color: white;
					}
					.MuiButtonBase-root{
						padding: 0;
						color: white;
					}
					p{
						margin: 0;
						font-size: 16px;
						letter-spacing: 1px;
					}
				}
			}
			.MuiTypography-root{
				font-size: 14px;
			}
			.MuiCollapse-container{
				.MuiExpansionPanelDetails-root{
					padding-left: 25px;
					padding-right: 5px;
					padding-top: 30px;
					padding-bottom: 30px;
				}
				.MuiButtonBase-root{
					padding: 0 5px;
				}
			}
		}

		.budgetDiv{
			.MuiPaper-root{
				box-shadow: none;
				.top{
					// padding: 0;
					min-height: 33px !important;
					background-color: #345581;
					border-radius: 0px;
					.MuiExpansionPanelSummary-content{
						margin: 0;
						color: white;
					}
					.MuiButtonBase-root{
						padding: 0;
						color: white;
					}
					p{
						margin: 0;
						font-size: 16px;
						letter-spacing: 1px;
					}
				}
			}
			.MuiTypography-root{
				font-size: 14px;
			}
			.MuiCollapse-container{
				.MuiExpansionPanelDetails-root{
					padding-left: 25px;
					padding-right: 5px;
					padding-top: 30px;
					padding-bottom: 30px;
				}
				.MuiButtonBase-root{
					padding: 0 5px;
				}
			}
		}

		.widget_title {
			font-family: 'HKGrotesk';
			font-size: 15px;
			color: #272727;
			margin: 0 0 20px;
		}
		.tr-list {
			margin: 0;
			padding: 0;
			list-style: none;
			li{
				a {
					font-family: 'HKGrotesk';
					text-decoration: none;
					font-size: 16px;
					padding: 5px 0;
					color: #898b8c !important;
					font-weight: 400;
				}
				a.active {
					color: #345581 !important;
				}
				a:hover {
					text-decoration: none;
					color: #345581 !important;
				}
			}
		}
		.margin-space {
			margin: 30px 0px;
		}
		.row{
			padding: 0px 0px!important;
			margin: 0px !important;
			.col-sm-6 {
				padding: 0px !important;
				margin: 0px !important;
			}
			.widget_title_small {
				font-family: 'HKGrotesk';
				font-size: 12px;
				color: #272727;
				margin: 0 0 4px 0px;
			}
		}
	}
}
.card-box {
	min-height: 440px;
	border-bottom: 3px solid transparent;
	transition: 0.5s;
	&:hover{
		border-bottom: 3px solid #345581;
	}
	.img-circle {
		border-radius: 50%;
		.img-circle{
			border-color: #345581 !important;
			border-width: 3px !important;
		}
	}
	background-color: #fff;
	padding: 10px 10px 10px 10px;
	color: #000;
	border-radius: 4px;
	margin-bottom: 30px;
	position: relative;
	overflow: hidden;
	.text{
		color: #898b8c;
	}
}
.favorite{
		outline: none;
	   float: right;
   }
   .card-box .most-popular {
	position: absolute;
	top: 25px;
	right: -60px;
   }	
   .card-box .most-popular span {
	font-family: 'HKGrotesk';
	color: #fff;
	display: block;
	font-size: 14px;
	padding: 5px 70px;
	background: #d65456 !important;
	-webkit-transform: rotate(40deg);
	-moz-transform: rotate(40deg);
	-ms-transform: rotate(40deg);
	-o-transform: rotate(40deg);
	transform: rotate(40deg);
   }
   .card-box .member-card .member-thumb {
	.img-circle{
		border-color: #345581 !important;
		border-width: 3px !important;
		height: 100%;
	}
	position: relative;
	height: 120px;
	width: 120px;
	margin-bottom: 10px !important;
	display: block;
	margin-right: auto;
	margin-left: auto;
   }
   .card-box i.member-star{
	position: relative;
	top: -120px;  
	left: 40px;
	color: #345581;
   }
   .card-box h4{
	   margin-top: 15px;
   }
   .card-box h4 a{
	margin-bottom: 0px !important;
	font-family: 'HKGrotesk';
	font-size: 19px;
	font-weight: 500;
	color: #345581 !important;
	line-height: 0 !important; 
   }
   .card-box h4.top{
	margin-bottom: 0px !important;
	font-family: 'HKGrotesk';
	font-size: 19px;
	font-weight: 700;
	color: #345581 !important;
	line-height: 1 !important; 
   }
   .card-box p.p-location {
	font-family: 'HKGrotesk';
	font-size: 14px;
	color: #898b8c !important;
	line-height: 0 !important;
   }
   .card-box i.rating-star{
	color: #345581;
   }
   .card-box p.p-star {
	font-family: 'HKGrotesk';
	font-size: 14px;
	color: #898b8c !important;
	line-height: 0 !important;
   }
   .card-box h5 {
	margin-top: 30px !important;
	margin-bottom: 10px !important;
	font-family: 'HKGrotesk';
	font-size: 15px;
	color: #345581 !important; 
	line-height: 20px;
	font-weight: 700;
   }
   .text-muted {
	color: #898b8c !important;
   }
   .featured-users p.font-16 {
	font-family: 'HKGrotesk';
	font-size: 15px;
   }
   .card-box .row{
	padding: 0px 5px!important;
	margin: 0px !important; 
   }
   .card-box .row .col-sm-4{
	padding: 0px !important;
	margin: 0px !important; 
   }
   .card-box h6 {
	 margin-bottom: 0px !important;
	 font-family: 'HKGrotesk';
	 font-size: 14px;
	 color: #345581 !important; 
   }
   .card-box p.bottom {
	 font-family: 'HKGrotesk';
	 font-size: 13px !important;
	 color: #898b8c !important;
	 margin-bottom: 0px !important;
   }

