}

.job {
    padding: 10px 0 0px 0;
    background-color: white;
    margin-top: 20px;
}

.job:hover{
    box-shadow: 0 5px 5px rgba(0,0,0,0.1);
    transition: 0.5s;
}



.job:last-child {
    content: "";
    width: 100%;
    height: 100%;
    display: inline-block;
    border-bottom: 4px solid #e0e0e0;
    margin-bottom: 5px;
}

.job .top-sec {
    padding-top: 15px;
    width: 100%;
    margin-left: 20px;
}

.job .top-sec .col-lg-2 {
    padding-right: 0 !important;
    margin-right: 0 !important;
}

.jobpost .col-lg-8.white .job .top-sec .col-lg-10, .col-lg-8.white-2 .job .top-sec .col-lg-10 {
    position: relative;
    left: -50px;
}

.job .top-sec .col-lg-10 {
    position: relative;
    left: -60px;
}

.nopadding {
    padding: 0 !important;
    margin: 0 !important;
}

.job .top-sec img {
    width: 60px;
    height: 60px;
    border-radius: 3px;
    transition: all .4s ease;
}

.job .top-sec img:hover, .job .top-sec img:focus {
    transform: scale(1.1);
}

.job .top-sec h4 a {
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
    font-weight: 700;
    color: #2c3e50;
    letter-spacing: 1px;
}

.job .top-sec h4:hover, .job .top-sec h4:focus {
    color: #868585 !important;
}

.job .top-sec h5 {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    color: #2c3e50;
    letter-spacing: 1px;
}

.job .top-sec h5 a {
    text-decoration: none;
}

.job .mid-sec {
    padding: 10px 0 5px 0;
}

.job .mid-sec p {
    font-family: 'Varela Round', sans-serif;
    padding-bottom: 0px;
    font-size: 14px;
    color: #868585 !important;
}

.job .mid-sec .label-success {
    font-family: 'Varela Round', sans-serif;
    background: rgb(219, 218, 218) !important;
    margin-bottom: 5px;
    font-size: 10px;
    color: #000 !important;
    letter-spacing: 1px;
}

.job .mid-sec .label-success:hover{
    color: white !important;
    background-color: #345581 !important;
    cursor: pointer;
}



.job .bottom-sec {
    padding-top: 10px;
}

.job .bottom-sec .kafe-btn-mint-small {
    margin-top: 10px;
}

.job .bottom-sec h5 {
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    color: #2c3e50;
    font-size: 12px !important;
    letter-spacing: 1px;
}

.job .bottom-sec p {
    font-family: 'Varela Round', sans-serif;
    padding-bottom: 0px;
    font-size: 14px;
    color: #868585 !important;
}



.post-btn{
    background-color: #345581 !important;
    color: white !important;
    border: 1px solid #345581 !important;
}





@media (min-width:768px){
#basic-navbar-nav{
    max-width: 300px !important;
}
}


